@import '../styles/variables-mixins.scss';

body {

    .fundpage {

        .profiles {
            background   : #F8F9FE;
            border       : 0.5px solid rgba(255, 247, 204, 0.25);
            margin-bottom: 36px;

            >section {
                padding-top: 36px;

                >h2 {
                    font-size    : 36px;
                    margin-bottom: 36px;
                }

                .offers {

                    >h3 {
                        font-size: 26px;
                    }

                    article {
                        display                 : grid;
                        grid-auto-flow          : column;
                        // grid-template-columns: repeat(auto-fill, 278px); // for visible case
                        gap                     : 52px;
                        margin-bottom           : 16px;
                        padding-top             : 36px;
                        padding-bottom          : 20px;
                        overflow-x              : auto;

                        >div {
                            @include border_radius;
                            width     : 278px;
                            padding   : 10px 24px 26px;
                            text-align: center;
                            border    : 0.5px solid rgba(0, 0, 0, 0.15);
                            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                            background: #FFFFFF;

                            .aum {
                                @include flex_center_bet;
                                font-size    : 18px;
                                font-weight  : 700;
                                margin-bottom: 6px;

                                span {
                                    &:nth-of-type(2) {
                                        font-weight : 400;
                                        margin-left : auto;
                                        margin-right: 10px;
                                    }
                                }
                            }

                            .api {
                                font-size    : 18px;
                                margin-bottom: 12px;

                                span {
                                    &:nth-of-type(1) {
                                        border-bottom: 2px solid rgba(0, 0, 0, 0.2);
                                        color        : rgba(0, 0, 0, 0.5);
                                    }
                                }
                            }

                            svg {
                                width        : 71px;
                                height       : 71px;
                                margin-bottom: 13px;
                            }

                            h4 {
                                @include flex_start;
                                font-size    : 18px;
                                font-weight  : 700;
                                margin-bottom: 16px;

                                a {
                                    display    : inherit;
                                    margin-left: 5px;
                                    font-weight: 400;
                                    color      : #000000;

                                    svg {
                                        margin-left: 5px;
                                        width      : 17px;
                                        height     : 17px;
                                    }
                                }
                            }

                            button {
                                @include yellow_button;
                                @include hover_active;
                                height       : 40px;
                                width        : 96px;
                                padding      : unset;
                                border-radius: unset;

                                &.soon {
                                    background: #DCDCD9;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}